<style>
</style>


<template>
  <div>
    <Row>
      <i-col span="24"
        class="bread-crumb">
        <Breadcrumb>
          <BreadcrumbItem v-for="(item,index) in breadcrumbList"
            :to="item.path"
            :key="index">
            <Icon :type="item.icon"></Icon> {{item.name}}
          </BreadcrumbItem>
        </Breadcrumb>
      </i-col>

    </Row>
    <Divider />
    <Row>
      <i-col span="12"
        offset="6">
        <Form ref="nginxNodeInfoFrom"
          :model="nginxNodeInfoFrom"
          :rules="fromValidate"
          :label-width="150">

          <FormItem label="IP"
            prop="ipAddress">
            <i-input v-model="nginxNodeInfoFrom.ipAddress"
              placeholder="IP"></i-input>
          </FormItem>

          <FormItem label=""
            prop="accessPort">
            <i-input v-model="nginxNodeInfoFrom.accessPort"
              placeholder=""></i-input>
          </FormItem>

          <FormItem label=""
            prop="accessUser">
            <i-input v-model="nginxNodeInfoFrom.accessUser"
              placeholder=""></i-input>
          </FormItem>

          <FormItem label=""
            prop="accessAuthType">
            <Select v-model="nginxNodeInfoFrom.accessAuthType"
              placeholder="">
              <Option v-for="item in accessAuthTypeEunmArr"
                :value="item.value"
                :disabled="item.disabled"
                :key="item.value">{{ item.label }}</Option>
            </Select>
          </FormItem>

          <FormItem label=""
            :rules="accessPasswordValidateRules()"
            v-show="showOnPassword()"
            prop="accessPassword">
            <i-input v-model="nginxNodeInfoFrom.accessPassword"
              :disabled="nginxNodeInfoFrom.accessAuthType != accessAuthTypeEunmArr[0].value"
              placeholder=""></i-input>
          </FormItem>

          <FormItem label=""
            :rules="accessRsaPathValidateRules()"
            v-show="showOnRsa()"
            prop="accessRsaPath">
            <i-input v-model="nginxNodeInfoFrom.accessRsaPath"
              :disabled="nginxNodeInfoFrom.accessAuthType != accessAuthTypeEunmArr[1].value"
              placeholder=""></i-input>
          </FormItem>

          <FormItem label=""
            prop="confPath">
            <i-input v-model="nginxNodeInfoFrom.confPath"
              placeholder=""></i-input>
          </FormItem>

          <FormItem>
            <Button type="primary"
              @click="handleSubmit('nginxNodeInfoFrom')"></Button>
          </FormItem>
        </Form>
      </i-col>
    </Row>
  </div>

</template>


<script>
export default {
  props: {
    query: { userId: 0 }
  },
  data() {
    return {
      routerPath: this.$route.path,
      accessAuthTypeEunmArr: [
        {
          value: '0',
          label: ''
        },
        {
          value: '1',
          label: ''
        }
      ],
      nginxNodeInfoFrom: {
        id: this.$route.params.id,
        serverId: '',
        apiName: ''
      },
      fromValidate: {
        ipAddress: [
          {
            required: true,
            pattern: /^(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])\.(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])\.(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])\.(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])$/,
            message: 'ip',
            trigger: 'blur'
          }
        ],
        accessPort: [
          {
            required: true,
            pattern: /^[0-9]{4,}$/,
            message: '',
            trigger: 'blur'
          }
        ],
        accessUser: [
          {
            required: true,
            max: 20,
            pattern: /^[a-zA-Z]+$/,
            message: ',,20',
            trigger: 'blur'
          }
        ],
        accessAuthType: [
          {
            required: true,
            message: '',
            trigger: 'change'
          }
        ],
        accessPassword: [
          {
            required: true,
            max: 20,
            mix: 5,
            message: ',5,20',
            trigger: 'blur'
          }
        ],
        accessRsaPath: [
          {
            required: true,
            pattern: /^\/\S*[A-Za-z0-9]+$/,
            message: ',`/` ',
            trigger: 'blur'
          }
        ],
        confPath: [
          {
            pattern: /^\/\S*[A-Za-z0-9]+$/,
            required: true,
            message: ' `/` ',
            trigger: 'blur'
          }
        ]
      }
    }
  },
  mounted: function() {
    this.loadData()
  },
  computed: {
    breadcrumbList: function() {
      return this.utils.routerUtil.initRouterTreeNameArr(this.routerPath)
    }
  },
  methods: {
    handleSubmit(name) {
      this.$refs[name].validate(valid => {
        if (valid) {
          this.commitData()
        } else {
          this.$Message.error('!')
        }
      })
    },
    loadData() {
      this.utils.netUtil.post(this.$store,
        this.API_PTAH.nginxNodeInfoFindById,
        this.nginxNodeInfoFrom,
        response => {
          this.nginxNodeInfoFrom = response.data.datas
        }
      )
    },
    commitData() {
      this.nginxNodeInfoFrom.id = this.$route.params.id
      this.utils.netUtil.post(this.$store,
        this.API_PTAH.nginxNodeInfoUpdate,
        this.nginxNodeInfoFrom,
        () => {
          this.$Message.success('!')
          this.$router.push('/nginx-node-info-list')
        }
      )
    },
    accessPasswordValidateRules() {
      let tempRuls = this.fromValidate.accessPassword
      let required =
        this.nginxNodeInfoFrom.accessAuthType !=
        this.accessAuthTypeEunmArr[1].value
      tempRuls[0].required = required
      return tempRuls
    },
    accessRsaPathValidateRules() {
      let tempRuls = this.fromValidate.accessRsaPath
      let required =
        this.nginxNodeInfoFrom.accessAuthType !=
        this.accessAuthTypeEunmArr[0].value
      tempRuls[0].required = required
      return tempRuls
    },
    showOnPassword() {
      return this.nginxNodeInfoFrom.accessAuthType == this.accessAuthTypeEunmArr[0].value
    },
    showOnRsa() {
      return this.nginxNodeInfoFrom.accessAuthType == this.accessAuthTypeEunmArr[1].value
    }
  }
}
</script>
